
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data () {
return {
id: '',
detail: ''
}
},
methods: {
formatePrice (num) {
return num / 100
},
getDetail () {
this.$tupHttp(this, 'front/account/charge/info/' + this.id).then(res => {
let detail = res.data.model
detail.createTime = new Date(detail.createTime).format('yyyy-MM-dd hh:mm:ss')
detail.amount = this.formatePrice(detail.amount)
if (detail.auditStatus) { // 线下充值才有这个字段
if (detail.auditStatus === '8796113240155' || detail.auditStatus === '8796113272923') {
detail.status = '审核中'
} else if (detail.auditStatus === '8796113305691' || detail.auditStatus === '8796113371227') {
detail.status = '审核未通过'
} else if (detail.auditStatus === '8796113338459') {
detail.status = '审核通过'
}
} else { // 线上充值
detail.status = detail.payStatus === 1 ? '充值成功' : '充值失败'
}
this.detail = detail
})
}
},
created () {
this.id = this.$route.query.id
this.getDetail(this.id)
},
components: {
XHeader
}
}



